@mixin ab($l, $r) {   
    @if $l != n { right: $l; }
    @if $r != n { left: $r; }
}
@mixin fixed($l, $r) {
    @if $l != n { right: $l; }
    @if $r != n { left: $r; }   
}
@mixin rotate($rotate) { 
  transform: rotate(($rotate) * (-1));
}
@mixin border-radius($tr, $tl, $bl, $br) {
  border-radius: $tl $tr $br $bl;
}
@mixin padding($pt, $pr, $pb, $pl) {
  padding: $pt $pl $pb $pr;
}

.theme-rtl {
	direction: rtl;

/*reset*/
#back-to-top {
  @include fixed(auto,20px);
}
.js-slider-controls {
  a svg {
    transform: rotate(180deg);
  }
  .js-next-button {
    @include ab(auto, 15px);
  }
  .js-prev-button {
    @include ab(auto, 55px);
  }
}
.breadcrumb-list .svg-element-ui svg {
  transform: rotate(180deg);
}
.label_ui-group {
  @include ab(-14px,auto);
  @media (min-width: 768px) {
    @include ab(-29px,auto);
  }
  >span {
    &:before {
      @include rotate(45deg);
      @include ab(0, auto);
    }
  }
}
.product-card .featured_product-overlay {
  @include ab(auto, 0);
}
.modal .modal_close {
  @include ab(auto,0);
}
.modal-currency-vs-language {
  .modal_box .select-group svg {
    @include ab(auto, 10px);
  }
}
.drawer-cart .btn-close {
  @include ab(0, auto);
}
.drawer-cart .lazy-drawer-cart {
  @include fixed(auto, 0);
  transform: translateX(-100%);
}
.drawer-cart.is-open .lazy-drawer-cart {
 transform: translateX(0); 
}
.header-cart_dropdown {
  @include ab(auto, 0);
  .lazy-dropdown-cart {
    &:before {
      @include ab(auto, 98px);
    }
  }
}
.pagination-holder .pagination .svg-element-ui svg {
  transform: rotate(180deg);
}
/*404*/
.page-404 .homepage-redirect svg {
  @include rotate(180deg);
}

/*end reset*/
/*topbar*/
.announcement-bar .annoubar-column .account .account-action a:first-child:after {
  @include ab (auto, 0);
}
.announcement-bar .annoubar-column .account>:first-child {
  padding-inline-start: 0;
}
/*header*/
.header-container {
  .vertical-nav {
    .dropdown-menu {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  .header-content .horizontal-nav .label {
    @include ab(auto, -25px);
  }
  .header-content .number {
    @include ab(auto,-8px);
  }
  .horizontal-nav>ul li.dropdown-submenu {
    li.dropdown-submenu .dropdown-menu {
      @include ab(100%, auto);
    }
  }
  .header-content .header-search {
    .form-search {
      input {
        @include border-radius(3px, 3px, 3px, 3px);
      }
      button {
        @include border-radius(0, 3px, 3px, 0);
        @include ab(auto,0);
      }
    }
    .search-results {
      @include border-radius(0, 0, 3px, 3px);
    }
  }
  @media (min-width: 992px) {
    .header-style-5 .header-content .header-helpline {
      .helpline-icon svg {
        transform: rotate(270deg);
      }
    }
  }
}
.main-menu .menu-item .dropdown-menu ul>li svg {
  @include rotate(180deg);
}
.header-mobile {
  .header-mobile__item .menu-list>li svg {
    @include rotate(180deg);
  }
  .header-mobile__item .menu-list>li {
    .btn-back {
      svg {
        @include rotate(0);
      }
    }
  }
  .header-mobile__item {
    .menu-list {
      .btn-close {
        @include ab(auto, 15px);
      }
      .btn-back {
        @include ab(15px, auto);
      }
      >li {
        .label {
          @include ab(8px, auto);
        }
      }
    }
  }
}
.mobile-bar .mobile-bar_item .number {
  @include ab(auto, -12px);
}
.drawer-search .form-search {
  input {
    @include border-radius(4px,0,0,4px);
  }
  .btn {
    @include border-radius(0,4px,4px,0);
  }
}
/*end headedr*/

/*slide show*/
.home-slideshow, .logo-list, .slideshow-v2, .home-slideshow-banner {
  .js-slider-controls {
    a {
      svg {
        transform: rotate(180deg);
      }
    }
    .js-next-button {
      @include ab(auto, -10px);
    }
    .js-prev-button {
      @include ab(-10px, auto);
    }
  }
  .tns-outer {
    .tns-horizontal {
      float: none;
      clear: both;
    }
  }
}
.slideshow-v2 .slideshow-text {
  .countdown {
    .seconds, .minutes, .hours {
      &:before {
        @include ab(-3px, auto);
      }
    }
  }
}
.home-slideshow .js-slider-controls {
  .js-next-button {
    left: 3%;
  }
  .js-prev-button {
    right: 3%;
  }
}
.home-slideshow-menu .section-content__menu {
  .menu-list li .js-btn-collapse {
    @include ab(auto, 0);
    svg {
      @include rotate(180deg);
    }
  }
}
/*end slide show*/
/*navigation*/
.vertical-nav .menu-list>li.dropdown-submenu .dropdown-menu {
  @include ab(100%, auto);
}
.vertical-nav.active>.dropdown-menu .menu-list>li.dropdown-submenu .dropdown-menu {
  @include ab(101%, auto);
}
/*home-categories*/
.home-categories {
  .card_section-item-inner .btn-show-all>div svg {
    transform: rotate(180deg);
  }
}
/*end home-categories*/
/*img gallery*/
.home-gallery {
  @media (min-width: 1025px) {
    &.hover_style-2 .style-1 .card_section-content .card_section-button:before {
      @include ab(0,auto);
      transform: translateX(100%);
    }
    &.hover_style-2 .card_section-figure {
      transform: translate3d(10px,0,0);
    }
  }
}
/*product tab*/
.featured_group_tab-ui .featured_group_tab-content>.featured_group_tab-pane:not(.active) {
  @include ab(0,auto);
}
.home-products-tab .row {
  .js-slider-controls .js-next-button {
    left: 15px;
  }
  .js-slider-controls .js-prev-button {
    left: 55px;
  }
}
/*banner fillter*/
.single-banner-filter .tabs-content_wrapper .chosen-single>svg {
  @include ab(auto,15px);
}
@media (min-width: 992px) {
  .single-banner-filter .tabs-content_item .btn-reset {
    @include ab(auto, 0);
  }
  .single-banner-filter .tabs-content_wrapper .chosen-container:first-child {
    @include border-radius(4px,0,0,4px);
  }
  .single-banner-filter .tabs-content_wrapper .btn {
    @include border-radius(0,4px,4px,0);
  }
}
/*left column*/
.sb-left-column {
  .sb-widget .js-slider-controls .js-prev-button {
    @include ab(auto,40px);
  }
  .widget-menu svg {
    @include rotate(-90deg);
  }
}
/*collection*/
.featured_block-category {
  .menu_list-wrapper li .expand {
    &:before {
      @include ab(auto, 4px);
    }
    &:after {
      @include ab(auto, 0);
    }
  }
}
.featured_block-filter_group .direction-column ul li a {
  &:before {
    @include ab(0,auto);
  }
  &:after {
    @include ab(0,auto);
  }
}
#sidebarDrawer .sidebarDrawer-close {
  @include ab(90vw, auto);
  @media (min-width: 375px) {
    @include ab(330px, auto);
  }
}
#sidebarDrawer {
  @include ab(-100%,auto);
  &.is-open {
    @include ab(0,auto);
  }
}
/*page search*/
.page-search .page_search-form .btn {
  @include ab(auto, 0);
}
/*page cart*/
.cart-section {
  .cart-content .line-item .btn-remove {
    @include ab(auto,0);
  }
  .cart-content_redirect>a svg {
    @include rotate(180deg);
  }
}
/*page account*/
.page-account {
  .login-or:before {
    @include ab(50%, auto);
  }
@media (min-width: 992px) {
    .login-or:before {
      @include ab(0, auto);
    }
  }
}
/*product page*/
.product-template {
  &.template-default, &.template-with-sidebar {
    .label_ui-group {
      @include ab(10px, auto);
    }
  }
  .left-content {
    background: #dcde6f!important;
  }
  #product_thumbnails_media-mw {
    background: #3a16ff;
  }
  &.template-no-sidebar .label_ui-group {
    @include ab(-5px, auto);
  }
  .js-slider-controls {
    a {
      svg {
        @include rotate(-90deg);
      }
    }
    .js-prev-btn {
      @include ab(-5px, auto);
    }
    .js-next-btn {
      @include ab(auto, -5px);
    }
  }
  .product_swatch .swatch-item {
    .select-wrapper {
      &:before {  
      @include ab(auto, 37px);
      }
      select {
        padding: 0 15px 0 42px;
      }
      .svg_element-ui {
        @include ab(auto, 13px);
      }
    }
  }
  .product-description .product_tab-contents .js-tab-content:not(.active) {
    @include ab(0,auto);
  }
  @media (min-width: 768px) {
    &.template-no-sidebar .js-slider-controls {
      .js-prev-btn {
        @include ab(50%, auto);
      }
      .js-next-btn {
        @include ab(auto, 50%);
      }
    }
  }
}
/*template blog*/
.featured_block-search .input-group button {
  @include ab(auto, 0);
}
.page-article .article-content {
  .content blockquote:before {
    @include ab(3%, auto);
  }
  .pagination {
    .prev svg {
      transform: rotate(180deg);
    }
    .next svg {
      transform: rotate(180deg);
    }
  }
}
/*footer*/
.footer .list-style li:before {
  @include ab(0,auto);
}
}